import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back,
    },
    data(){
        return{
            user: this.$store.state.userInfo,
            SubjectBuyRecordId:this.$route.query.SubjectBuyRecordId,
            list:[],
            showConfirm:false,
        }
    },
    created(){
        this.getLookMDTMedical()
    },
    mounted(){
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
         getImg(idx){
            // this.$bridge.callhandler('printdata',1213)
            this.$bridge.callhandler('APP', 'Img', (data) => {
                // this[target].push()
                const DisplayPath = data.split(',')[0].split('=')[1]
                const SavePath = data.split(',')[1].split('=')[1].split('}')[0]
                this.list[idx].dataList.push({
                    DisplayPath,
                    SavePath
                })
                this.showConfirm = true
                this.showConfirm = false
            })
            this.showConfirm = true
            this.showConfirm = false
        },
		getLookMDTMedical(){
			this.$get('MDTAPI/MDTMedical/QueryLookMDTMedical',{
				SubjectBuyRecordId:this.SubjectBuyRecordId
			}).then(o => {
                this.list = o
                this.list.forEach((k,v) => {
                    this.list[v].dataList = []
                })
			})
        },
        send(){
            const ImgDetails = []
            this.list.forEach(v => {
                const imgList = []
                v.dataList.forEach(k => {
                    imgList.push(k.SavePath)
                })
                ImgDetails.push({
                    MedicalId:v.MedicalId,	//诊疗病历Id
                    Remark:v.MedicalId,	//备注
                    ImagePaths:imgList //诊疗病历图片地址
                })
            })
            this.$post('MDTAPI/MDTMedical/SaveMedical',{
                SubjectBuyRecordId:this.SubjectBuyRecordId,
                DrId:this.user.Data.User.DoctorId, 
				DrName:this.user.Data.User.DoctorName, 
                MedicalPaths:ImgDetails
            }).then(o => {
                this.$toast('上传成功')
                this.$router.back()
            })
        },
        pop(idx,idy){
            this.list[idx].dataList.splice(idy,1)
            this.showConfirm = true
            this.showConfirm = false
        },
        goBack(){
            this.showConfirm = true
        },
        confirm(){
			this.$router.back()
		},
    }
}



